<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process169"></a>

<p><br></p>
<p><span class="rvts17">瞌睡的冒险家（</span><span class="rvts50">The Snoozing Adventurer</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:220,Y:764</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：瞌睡的冒险家</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">偷走</span><span class="rvts20"></span><span class="rvts21">会说话的箱子</span><span class="rvts20"></span><span class="rvts21">里面的东西</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1172.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">瞌睡的冒险家位于浮木城酒馆上层的一个休息间内。首先需要和他进行对话，他会在梦里说一些胡话。</span>
<br><span class="rvts21">之后，尝试打开他床边的那个箱子，发现箱子竟然可以说话！但是打开箱子需要密码，接着需要询问这个瞌睡虫的密码（假扮成瞌睡虫的妈妈）。</span>
<br><span class="rvts21">获得密码后，可以打开箱子，获得里面的物品。如果选择攻击箱子，那么箱子会启动自毁程序。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



